import { firebaseConfig, GameReducerType_ReactionTime, GameReducerType_FraudDetection } from "../constants";
import * as firebase from 'firebase';

// Initialize Firebase
firebase.initializeApp(firebaseConfig);

const removeSpecialChars = (string) => {
    return string.replace(/[^a-zA-Z0-9]/g, "")
}

export const postScore = (user, gameType, score) => {
    return new Promise((resolve, reject) => {
        const { name, email, mobile: phone, college } = user;
        const timestamp = Date.now();
        console.log("DataService submitScore:");
        console.log(user, gameType, score);

        var userId = removeSpecialChars(name) + "-" + removeSpecialChars(email) + "-" + removeSpecialChars(phone);
        console.log("user id : " + userId)

        try {
            const ref = firebase.database().ref('user-scores/' + gameType + '/' + userId);
            ref.transaction((data) => {
                if (data !== null) {
                    if (gameType === GameReducerType_ReactionTime && data.score > score) {
                        data.score = score
                        data.timestamp = timestamp
                    } else if (data.score <= score) {
                        data.score = score
                        data.timestamp = timestamp
                    }
                    return data;
                }
                else {
                    return { userId, name: name, email: email, phone, gameType, score, college, timestamp }
                }
            });

            resolve(true);
        } catch (error) {
            console.log("Request failed ", error)
            reject(error);
        };
    });
}

export const fetchLeadershipBoardData = (gameType, limit = 10) => {
    return new Promise((resolve, reject) => {
        try {
            let gameTypeRef;

            if (gameType === GameReducerType_ReactionTime || gameType === GameReducerType_FraudDetection) {
                gameTypeRef = firebase.database().ref('user-scores/' + gameType).orderByChild('score').startAt(1).limitToFirst(limit);
            } else {
                gameTypeRef = firebase.database().ref('user-scores/' + gameType).orderByChild('score').startAt(1).limitToLast(limit);
            }

            gameTypeRef.once('value').then(function (snapshot) {
                const results = snapshot.val()

                if (results != null) {
                    // Sort the data in descending order on score, Short coming of firebase
                    let userData = Object.keys(results).map((key) => { return results[key]; });

                    userData.sort(function (a, b) {
                        return (gameType === GameReducerType_ReactionTime || gameType === GameReducerType_FraudDetection) ? a.score - b.score : b.score - a.score;
                    });


                    userData = userData.map((data, idx) => ({ ...data, rank: idx + 1 }))
                    resolve(userData);
                } else {
                    resolve([]);
                }
            });
        } catch (error) {
            console.log("Request failed ", error)
            reject([]);
        };
    });
};